} /* ------------------------------------
            animation-element
--------------------------------------*/
.slide-from-top {
  transform: translateY(-50%);
  opacity: 0;
  transition: all 0.8s 0.8s ease-out;
}
.slide-from-top.in-view {
  transform: translateY(0);
  opacity: 1;
}
.slide-from-bottom-3s {
  transform: translateY(60%);
  opacity: 0;
  transition: all 0.8s 0.3s ease-out;
}
.slide-from-bottom-3s.in-view {
  transform: translateY(0);
  opacity: 1;
}
.slide-from-bottom-5s {
  transform: translateY(60%);
  opacity: 0;
  transition: all 0.8s 0.5s ease-out;
}
.slide-from-bottom-5s.in-view {
  transform: translateY(0);
  opacity: 1;
}
.slide-from-bottom-6s {
  transform: translateY(60%);
  opacity: 0;
  transition: all 0.8s 0.65s ease-out;
}
.slide-from-bottom-6s.in-view {
  transform: translateY(0);
  opacity: 1;
}
.slide-from-bottom-7s {
  transform: translateY(60%);
  opacity: 0;
  transition: all 0.8s 0.75s ease-out;
}
.slide-from-bottom-7s.in-view {
  transform: translateY(0);
  opacity: 1;
}
.slide-left-to-right-6s {
  transform: translateX(-60%);
  opacity: 0;
  transition: all 0.6s 1s ease-out !important;
}
.slide-left-to-right-6s.in-view {
  transform: translateX(0);
  opacity: 0.6;
}
.slide-right-to-left-6s {
  transform: translateX(60%);
  opacity: 0;
  transition: all 0.6s 1s ease-out !important;
}
.slide-right-to-left-6s.in-view {
  transform: translateX(0);
  opacity: 0.6;
}
.fade-left-to-right-6s {
  transform-origin: left;
  transform: scale(0, 0);
  opacity: 0;
  transition: all 0.8s 0.65s ease-out;
}
.fade-left-to-right-6s.in-view {
  transform: scale(1, 1);
  opacity: 1;
}
.fade-left-to-right-7s {
  transform-origin: left;
  transform: rotateX(180deg) scale(0, 0);
  opacity: 0;
  transition: all 0.8s 0.75s ease-out;
}
.fade-left-to-right-7s.in-view {
  transform: rotateX(180deg) scale(1, 1);
  opacity: 1;
}
.fade-in-3s {
  opacity: 0;
  transition: all 0.8s 0.3s ease-out;
}
.fade-in-3s.in-view {
  opacity: 1;
}
.fade-in-8s {
  opacity: 0;
  transition: all 0.8s 0.8s ease-out;
}
.fade-in-8s.in-view {
  opacity: 1;
}
.roll-from-left {
  transition: transform 0.3s ease-out, opacity 0.8s ease-out;
  opacity: 0;
  transform: translateX(-20%) rotateZ(-90deg);
}
.roll-from-left.in-view {
  opacity: 1;
  transform: translateX(0) rotateZ(0);
}
.roll-from-right {
  transition: transform 0.3s ease-out, opacity 0.8s ease-out;
  opacity: 0;
  transform: translateX(20%) rotateZ(90deg);
}
.roll-from-right.in-view {
  opacity: 1;
  transform: translateX(0) rotateZ(0);
}
.zoom-out {
  transform: scale(4, 4);
  transition: all 0.5s ease-out;
  width: 25% !important;
}
.zoom-out.in-view {
  transform: scale(1, 1);
  width: 100% !important;
}
