/* ------------------------------------
                Section1
--------------------------------------*/
.section1-bg {
  z-index: -1;
  width: 100%;
  position: absolute;
  background-image: linear-gradient(
      90deg,
      #26272c 16%,
      rgba(255, 255, 255, 0.123) 100%
    ),
    url("./image/section1-bg.jpg");
  background-size: cover;
  background-position: right center;
}
#section1 a.btn-outline-secondary {
  font-size: 11px !important;
}
#section1 .triangle-bottom-right {
  background: linear-gradient(to bottom right, transparent 50%, #26272c 50.5%)
      no-repeat bottom,
    /* bottom part */ linear-gradient(0deg, transparent, transparent) no-repeat
      top;
  height: 200px;
}
#section1 .triangle-bottom-right a:hover {
  cursor: pointer;
}
#section1 .triangle-bottom-right i {
  font-size: 24px;
  padding: 0.5rem;
  border: 0.5px solid rgba(255, 255, 255, 0.1);
}
#section1 .triangle-bottom-right a:hover i {
  cursor: pointer;
  animation: arrow 0.4s;
}
@keyframes arrow {
  0% {
    padding-top: 0.4rem;
    padding-bottom: 0.6rem;
  }
  100% {
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
  }
  50% {
    padding-top: 0.8rem;
    padding-bottom: 0.2rem;
  }
}
