/* ------------------------------------
                Section6
--------------------------------------*/
#section6 .heading h1 {
  width: 500px;
}
.carousel {
  height: 300px;
  overflow: hidden;
  position: relative;
  margin: none auto;
}
.carousel .content {
  display: flex;
  height: 100%;
  transition: 500ms ease-in-out;
}
.carousel .content > * {
  min-width: 100%;
}
.carousel .navigation {
  transform: translateY(-250%);
}
.carousel .navigation div {
  cursor: pointer;
  font-size: 1.8rem;
  padding: 5px;
  border: 1px solid #f7f7f713;
  color: #fff;
  transition: 300ms;
}
@keyframes border-animation {
  0% {
    background-size: 1px 0, 0 1px, 1px 0, 0 1px;
  }
  25% {
    background-size: 1px 100%, 0 1px, 1px 0, 0 1px;
  }
  50% {
    background-size: 1px 100%, 0 1px, 1px 0, 100% 1px;
  }
  75% {
    background-size: 1px 100%, 0 1px, 1px 100%, 100% 1px;
  }
  100% {
    background-size: 1px 100%, 100% 1px, 1px 100%, 100% 1px;
  }
}
.carousel .item {
  margin: 2rem auto;
  padding: 2em;
  border-radius: 3px;
  background-repeat: no-repeat;

  max-width: 700px;
}
.carousel .item.border-animation-section6.in-view {
  background-image: linear-gradient(to right, #009946 100%, #009946 100%),
    linear-gradient(to bottom, #009946 100%, #009946 100%),
    linear-gradient(to right, #009946 100%, #009946 100%),
    linear-gradient(to bottom, #009946 100%, #009946 100%);
  background-size: 100% 1px, 1px 100%, 100% 1px, 1px 100%;
  background-position: 0 0, 100% 0, 100% 100%, 0 100%;
  animation: border-animation 0.8s cubic-bezier(0.19, 1, 0.22, 1) 1;
}
.carousel .item img {
  height: 90px;
  border-radius: 50%;
  border: 7px solid #26272c;
  margin-top: -5.5rem !important;
}
/* ------------------------------------
                Section7
--------------------------------------*/
#section7 {
  background-image: linear-gradient(
      0deg,
      rgba(38, 39, 44, 1) 0%,
      rgba(38, 39, 44, 1) 50%,
      rgba(38, 39, 44, 0) 50%
    ),
    linear-gradient(90deg, #26272c 6%, rgba(255, 255, 255, 0.123) 100%),
    url("./image/section7-bg.jpg");
  background-size: cover;
  background-position: right center;
  background-attachment: fixed;
}
#section7 .div-bottom {
  background-color: #26272c;
}
#section7 .div-bottom p {
  max-width: 500px;
}
#section7 .div-bottom input {
  border-radius: 0;
  border: 1px solid #f7f7f713;
  background-color: #26272c;
  color: #fff;
}
#section7 .div-bottom input::placeholder {
  color: rgb(182, 182, 182);
  font-weight: 900;
}
#section7 .div-bottom input:focus {
  -webkit-box-shadow: 0px 0px 5px 0px rgba(0, 153, 69, 1);
  -moz-box-shadow: 0px 0px 5px 0px rgba(0, 153, 69, 1);
  box-shadow: 0px 0px 5px 0px rgba(0, 153, 69, 1);
}
/* ------------------------------------
                Section8
--------------------------------------*/
#section8 .heading p {
  max-width: 500px;
}
#section8 .product {
  background: none;
  border: none;
  max-width: 130px;
  margin: 0 0.8rem;
}
#section8 .product:hover .card-body,
#section8 .product:hover .card-footer {
  background-color: #009946;
  cursor: pointer;
}
.animation-2s {
  transition: all 0.4s 0.2s ease-in-out;
}
.animation-3s {
  transition: all 0.4s 0.3s ease-in-out;
}
.animation-4s {
  transition: all 0.4s 0.4s ease-in-out;
}
.animation-5s {
  transition: all 0.4s 0.5s ease-in-out;
}
.animation-6s {
  transition: all 0.4s 0.6s ease-in-out;
}
.animation-7s {
  transition: all 0.4s 0.7s ease-in-out;
}
.animation-8s {
  transition: all 0.4s 0.8s ease-in-out;
}
#section8 .roll-from-left {
  transition-duration: 0.2s;
}
.scaleY-from-top {
  transform-origin: top;
  transform: scaleY(0);
}
.scaleY-from-top.in-view {
  transform-origin: top;
  transform: scaleY(1);
}
#section8 .card-body {
  transition: background-color 0.2s ease-in-out;
  background-color: #1e1f24;
  /*#009946;*/
  border-top-left-radius: 3px;
  border-top-right-radius: 3px;
  -webkit-box-shadow: 0px 0px 16px -2px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 0px 0px 16px -2px rgba(0, 0, 0, 0.75);
  box-shadow: 0px 0px 16px -2px rgba(0, 0, 0, 0.75);
}
#section8 .card img {
  z-index: 1;
}
#section8 .card-footer {
  transition: background-color 0.2s ease-in-out;
  background-color: #1e1f24;
  border-bottom-left-radius: 100%;
  border-bottom-right-radius: 100%;
  border: none;
  -webkit-box-shadow: 0px 10px 16px -6px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 0px 10px 16px -6px rgba(0, 0, 0, 0.75);
  box-shadow: 0px 10px 16px -6px rgba(0, 0, 0, 0.75);
}
#section8 .product .card-body h6 {
  width: 80px;
}
